:root {
  --highlight-color: hsla(209, 27%, 79%, 0.397);
  --main-color: hsl(209, 27%, 23%);
}
#root{
  height: 100vh;
}

html, body{
  color: var(--main-color);
  height: 100vh;
  margin: 0;
  overflow-x: hidden;
}

