img {
  width: 100%
}

/* Places Components*/
.container {
  display: block;
  height: 100vh;
}

.info {
  position: relative;
  margin: 0px 10px 60px;
  padding: 10px;
  box-sizing: border-box;
  width: 100%;
  display: block;
  z-index: 5;
}

blockquote {
  line-height: 16px;
  font-style: italic;
  font-family: serif;
  font-family: 'Gilda Display', serif;
  text-transform: lowercase;
  padding: 20px;
}

.cite {
  text-align: right;
  margin-left: 60px;
}

.cite:hover {
  color: var(--highlight-color);
}
button {
  position: absolute;
  top: 0;
  align-self: flex-start;
  background-color: white;
  border: none;
  font-size: 18px;
  position: fixed;
  right: 0;
  padding: 5px 10px;
  font-size: 14px;
}
button a{
  transition: 0.3s ease-out;
}

button a:hover {
  color: var(--highlight-color);
}

h1 {
  padding: 5px;
  margin: 0;
  /* z-index: 1; */
  width: 100%;
  position: absolute;
  top: 0;
  background-color: white;
  color: var(--main-color);
}

h3{
  width: 100%;
}

h4 {
  font-size: 46px;
  margin: 0;
  margin-top: 60px;
}

h5 {
  font-size: 22px;
  margin: 0 0 25px 0;
  text-transform: lowercase;
}

p {
  margin: 5px;
  font-size: 12px;
}

h2 {
  font-family: serif;
  margin: 0;
  width: 100%;
  overflow: hidden;
}

html, body>* {
  font-family: 'Questrial', sans-serif;
  font-size: 10px;
  text-transform: uppercase;
}

.link, a {
  color: var(--main-color);
  text-decoration: none;
}
.project-title h3 {
  font-weight: 100;
  font-size: 48px;
  font-family: 'Gilda Display', serif;
  letter-spacing: -5px;
  margin: 0;
}

.project-title h3:hover {
  position: relative;
  z-index: 3
}

.project-categ {
  font-weight: lighter;
  text-transform: lowercase;
  font-family: 'Questrial', sans-serif;
}



.wrapper {
  margin: 180px auto;
  height: 100%;
  position: relative;
}

.project>* {
  padding: 12px;
  position: relative;
  transition: margin-left .1s ease-in-out;
}

.project-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}



@media (min-width: 900px) {
  h1 {
    background-color: transparent;
    mix-blend-mode: exclusion;
  }
  .container {
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
    align-items: flex-start;
    height: 100vh;
  }
  .info {
    height: 100vh;
    overflow: hidden;
    flex: 1;
    margin: 0 10px;
  }
  .cursor {
  position: absolute;
  width: 450px;
  height: 200px;
  top: 50%;
  left: 50%;
  margin: -100px 0 0 -100px;
  background: url(https://images.unsplash.com/photo-1489058535093-8f530d789c3b?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1050&q=80) no-repeat 50% 50%;
  background-size: cover;
  border: 0.1px solid var(--hightlight-color);
}
  .images{
    flex: 1.5;
    overflow: auto;
    height: 100vh;
  }
  button {
    z-index: 20;
    text-justify: right;
    margin: 20px;
  }
  .project>* {
    color: var(--highlight-color);
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .project>*:hover {
    font-style: oblique;
    color: var(--main-color);
    margin-left: 10px;
  }
  .images::-webkit-scrollbar {
    width: 9px;
  }
  .images {
    --scrollbarBG: white;
    scrollbar-width: thin;
    scrollbar-color: var(--main-color) var(--scrollbarBG);
  }
  .images::-webkit-scrollbar-track {
    background: var(--scrollbarBG);
  }
  .images::-webkit-scrollbar-thumb {
    background-color: var(--main-color) ;
    border-radius: 6px;
    border: 3px solid var(--scrollbarBG);
  }
}
